aBoldItalic'),\n  url('/fonts/Montserrat/montserrat-v12-cyrillic_latin-800italic.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */\n  url('/fonts/Montserrat/montserrat-v12-cyrillic_latin-800italic.woff2') format('woff2'), /* Super Modern Browsers */\n  url('/fonts/Montserrat/montserrat-v12-cyrillic_latin-800italic.woff') format('woff'), /* Modern Browsers */\n  url('/fonts/Montserrat/montserrat-v12-cyrillic_latin-800italic.ttf') format('truetype'), /* Safari, Android, iOS */\n  url('/fonts/Montserrat/montserrat-v12-cyrillic_latin-800italic.svg#Montserrat') format('svg'); /* Legacy iOS */\n}\n/* montserrat-800 - cyrillic_latin */\n@font-face {\n  font-family: 'Montserrat';\n  font-style: normal;\n  font-weight: 800;\n  src: url('/fonts/Montserrat/montserrat-v12-cyrillic_latin-800.eot'); /* IE9 Compat Modes */\n  src: local('Montserrat ExtraBold'), local('Montserrat-ExtraBold'),\n  url('/fonts/Montserrat/montserrat-v12-cyrillic_latin-800.eot?#iefix') format('embedded-opentype'), /* IE6-IE8 */\n  url('/fonts/Montserrat/montserrat-v12-cyrillic_latin-800.woff2') format('woff2'), /* Super Modern Browsers */\n  url('/fonts/Montserrat/montserrat-v12-cyrillic_latin-800.woff') format('woff'), /* Modern Browsers */\n  url('/fonts/Montserrat/montserrat-v12-cyrillic_latin-800.ttf') format('truetype'), /* Safari, Android, iOS */\n  url('/fonts/Montserrat/montserrat-v12-cyrillic_latin-800.svg#Montserrat') format('svg'); /* Legacy iOS */\n}\n","@import \"fonts.scss\";\n\n$black: #0a0b0a;\n$orange: #ff5100;\n$white: #fff;\n$grey: #8b9497;\n$bgDark: #1a1a1a;\n$brown: #484848;\n\n@mixin base-sizes() {\n  &__size {\n    &_l {\n      width: 190px;\n    }\n    &_s {\n      width: 85px;\n    }\n    &_full {\n      width: 100%;\n      height: 44px;\n    }\n  }\n}\n\n@mixin placeholder-color($color) {\n  &::-webkit-input-placeholder {\n    color: $color;\n  }\n\n  &::-moz-placeholder {\n    color: $color;\n  }\n\n  &:-moz-placeholder {\n    color: $color;\n  }\n\n  &:-ms-input-placeholder {\n    color: $color;\n  }\n}\n\n@mixin base-margins() {\n  &__margin {\n    &_left {\n      margin-left: 10px;\n      &_x2 {\n        margin-left: 20px;\n      }\n    }\n    &_right {\n      margin-right: 10px;\n      &_x2 {\n        margin-right: 20px;\n      }\n    }\n    &_top {\n      margin-top: 10px;\n      &_x2 {\n        margin-top: 20px;\n      }\n    }\n    &_bottom {\n      margin-bottom: 10px;\n      &_x2 {\n        margin-bottom: 20px;\n      }\n    }\n  }\n}\n\n@mixin base-aligns() {\n  &__align {\n    &_left {\n      text-align: left;\n    }\n    &_center {\n      text-align: center;\n    }\n    &_right {\n      text-align: right;\n    }\n  }\n}\n\n@mixin main-font($weight) {\n  font-family: \"Montserrat\", sans-serif;\n  font-weight: $weight;\n}\n\nh1,h2,h3,h4,h5,h6,p {\n  padding: 0;\n  margin: 0;\n  font-size: 16px;\n}\nul,ol {\n  padding: 0;\n  margin: 0;\n  font-size: 0;\n}\ndiv {\n  &:focus {\n    outline: 0;\n  }\n}\na {\n  text-decoration: none;\n  color: $black;\n  font-size: 16px;\n}\nbody {\n  font-size: 13px;\n  @include main-font(500);\n  background-color: #000000;\n  padding: 0;\n  box-sizing: border-box;\n  margin: 0;\n  background-repeat: no-repeat;\n  background-position: top center;\n  > * {\n    box-sizing: border-box;\n  }\n}\n\n// Responsive settings\n$screenSizes: (\n        desktop: 1200px,\n        tablet: 991px,\n        mobile: 768px,\n        mobile-small: 480px,\n);\n\n@mixin screen-gt($name) {\n  @media only screen and (min-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin screen-lt($name) {\n  @media only screen and (max-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin container-size() {\n  width: 1320px;\n  margin: auto;\n  padding: 0 20px;\n  @include screen-lt(desktop) {\n    width: 100%;\n  }\n  @include screen-lt(mobile-small) {\n    padding: 0 10px;\n  }\n}\n\n@mixin bgImage($image) {\n  background-repeat: no-repeat;\n  background-position: top center;\n  background-image: url($image);\n  @include screen-lt(mobile-small) {\n    background-position: 51% 0;\n  }\n}","@import \"./src/theme/global.scss\";\n\n.modal {\n  display: none;\n  position: fixed;\n  align-items: center;\n  justify-content: center;\n  height: 100%;\n  z-index: 6;\n  top: 0;\n  left: 0;\n  width: 100%;\n  background-color: rgba(0, 0, 0, 0.8);\n  animation-name: animate-in;\n  animation-duration: .2s;\n\n  &_open {\n    display: flex;\n  }\n\n  &__inner {\n    width: 350px;\n    padding: 20px 40px;\n    background-color: $bgDark;\n    border: 1px solid $orange;\n    z-index: 20;\n    display: flex;\n    align-items: center;\n    position: relative;\n    justify-content: center;\n    flex-direction: column;\n    border-radius: 5px;\n    animation-name: animate-modal-in;\n    animation-duration: .2s;\n    @include screen-lt(mobile) {\n      width: 98%;\n    }\n  }\n  &_close {\n    border: 2px solid $orange;\n    width: 40px;\n    height: 40px;\n    display: flex;\n    align-items: center;\n    color: $orange;\n    top: -20px;\n    right: -20px;\n    cursor: pointer;\n    background-color: $bgDark;\n    position: absolute;\n    justify-content: center;\n    font-size: 20px;\n    text-align: center;\n    border-radius: 50%;\n    padding: 0;\n    outline: none;\n    &:hover {\n      background-color: #551E00;\n    }\n    @include screen-lt(mobile) {\n      top: 5px;\n      right: 5px;\n      border: none;\n    }\n  }\n}\n\n@keyframes animate-in {\n  0% {\n    background-color: rgba(0, 0, 0, 0);\n  }\n  100% {\n    background-color: rgba(0, 0, 0, 0.8);\n  }\n}\n\n@keyframes animate-modal-in {\n  0% {\n    top: -10px\n  }\n  100% {\n    top: 0;\n  }\n}"]}]);

