in-800.woff') format('woff'), /* Modern Browsers */\n  url('/fonts/Montserrat/montserrat-v12-cyrillic_latin-800.ttf') format('truetype'), /* Safari, Android, iOS */\n  url('/fonts/Montserrat/montserrat-v12-cyrillic_latin-800.svg#Montserrat') format('svg'); /* Legacy iOS */\n}\n","@import \"fonts.scss\";\n\n$black: #0a0b0a;\n$orange: #ff5100;\n$white: #fff;\n$grey: #8b9497;\n$bgDark: #1a1a1a;\n$brown: #484848;\n\n@mixin base-sizes() {\n  &__size {\n    &_l {\n      width: 190px;\n    }\n    &_s {\n      width: 85px;\n    }\n    &_full {\n      width: 100%;\n      height: 44px;\n    }\n  }\n}\n\n@mixin placeholder-color($color) {\n  &::-webkit-input-placeholder {\n    color: $color;\n  }\n\n  &::-moz-placeholder {\n    color: $color;\n  }\n\n  &:-moz-placeholder {\n    color: $color;\n  }\n\n  &:-ms-input-placeholder {\n    color: $color;\n  }\n}\n\n@mixin base-margins() {\n  &__margin {\n    &_left {\n      margin-left: 10px;\n      &_x2 {\n        margin-left: 20px;\n      }\n    }\n    &_right {\n      margin-right: 10px;\n      &_x2 {\n        margin-right: 20px;\n      }\n    }\n    &_top {\n      margin-top: 10px;\n      &_x2 {\n        margin-top: 20px;\n      }\n    }\n    &_bottom {\n      margin-bottom: 10px;\n      &_x2 {\n        margin-bottom: 20px;\n      }\n    }\n  }\n}\n\n@mixin base-aligns() {\n  &__align {\n    &_left {\n      text-align: left;\n    }\n    &_center {\n      text-align: center;\n    }\n    &_right {\n      text-align: right;\n    }\n  }\n}\n\n@mixin main-font($weight) {\n  font-family: \"Montserrat\", sans-serif;\n  font-weight: $weight;\n}\n\nh1,h2,h3,h4,h5,h6,p {\n  padding: 0;\n  margin: 0;\n  font-size: 16px;\n}\nul,ol {\n  padding: 0;\n  margin: 0;\n  font-size: 0;\n}\ndiv {\n  &:focus {\n    outline: 0;\n  }\n}\na {\n  text-decoration: none;\n  color: $black;\n  font-size: 16px;\n}\nbody {\n  font-size: 13px;\n  @include main-font(500);\n  background-color: #000000;\n  padding: 0;\n  box-sizing: border-box;\n  margin: 0;\n  background-repeat: no-repeat;\n  background-position: top center;\n  > * {\n    box-sizing: border-box;\n  }\n}\n\n// Responsive settings\n$screenSizes: (\n        desktop: 1200px,\n        tablet: 991px,\n        mobile: 768px,\n        mobile-small: 480px,\n);\n\n@mixin screen-gt($name) {\n  @media only screen and (min-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin screen-lt($name) {\n  @media only screen and (max-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin container-size() {\n  width: 1320px;\n  margin: auto;\n  padding: 0 20px;\n  @include screen-lt(desktop) {\n    width: 100%;\n  }\n  @include screen-lt(mobile-small) {\n    padding: 0 10px;\n  }\n}\n\n@mixin bgImage($image) {\n  background-repeat: no-repeat;\n  background-position: top center;\n  background-image: url($image);\n  @include screen-lt(mobile-small) {\n    background-position: 51% 0;\n  }\n}","@import \"./src/theme/global.scss\";\n\n.dropdown {\n  position: absolute;\n  top: 60px;\n  right: 20px;\n  width: 150px;\n  display: flex;\n  &:after {\n    content: '';\n    position: absolute;\n    top: -5px;\n    left: calc(150px / 2 - 2.5px);\n    background: $white;\n    width: 10px;\n    z-index: 1;\n    height: 10px;\n    transform: rotate(45deg);\n  }\n  &__button {\n    height: 35px;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background-color: $white;\n    z-index: 2;\n    border: none;\n    font-size: 14px;\n    color: $bgDark;\n    width: 100%;\n    cursor: pointer;\n    &:first-child {\n      border-top-left-radius: 5px;\n      border-top-right-radius: 5px;\n      border-bottom: 1px solid $grey;\n    }\n    &:last-child {\n      border-bottom-left-radius: 5px;\n      border-bottom-right-radius: 5px;\n    }\n    &:hover {\n      background-color: #ededed;\n      transition: .1s;\n    }\n  }\n}\n\n.header {\n  &__authorized {\n    position: relative;\n    z-index: 2;\n    cursor: pointer;\n    @include screen-lt(mobile) {\n      p {\n        font-size: 0;\n      }\n    }\n  }\n\n  &__user {\n    position: relative;\n    user-select: none;\n\n    &:before {\n      content: '';\n      position: absolute;\n      border-radius: 30px;\n      top: -12px;\n      left: -35px;\n      background-image: url('../../../statics/default_user_icon.png');\n      width: 25px;\n      height: 25px;\n      background-size: contain;\n      background-repeat: no-repeat;\n      @include screen-lt(mobile) {\n        width: 35px;\n        height: 35px;\n        top: -20px;\n      }\n    }\n  }\n  &__mobile {\n    @include screen-lt(tablet) {\n      position: absolute;\n      top: 0;\n      right: 15px;\n      .ux-button {\n        display: none;\n      }\n    }\n  }\n}\n\n.header__mobile_btn {\n  display: none;\n  @include screen-lt(tablet) {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    background-color: transparent;\n    border: none;\n    height: 100%;\n    width: 50px;\n    outline: none;\n    img {\n      width: 100%;\n    }\n  }\n}"]}]);

