 &_bottom {\n      margin-bottom: 10px;\n      &_x2 {\n        margin-bottom: 20px;\n      }\n    }\n  }\n}\n\n@mixin base-aligns() {\n  &__align {\n    &_left {\n      text-align: left;\n    }\n    &_center {\n      text-align: center;\n    }\n    &_right {\n      text-align: right;\n    }\n  }\n}\n\n@mixin main-font($weight) {\n  font-family: \"Montserrat\", sans-serif;\n  font-weight: $weight;\n}\n\nh1,h2,h3,h4,h5,h6,p {\n  padding: 0;\n  margin: 0;\n  font-size: 16px;\n}\nul,ol {\n  padding: 0;\n  margin: 0;\n  font-size: 0;\n}\ndiv {\n  &:focus {\n    outline: 0;\n  }\n}\na {\n  text-decoration: none;\n  color: $black;\n  font-size: 16px;\n}\nbody {\n  font-size: 13px;\n  @include main-font(500);\n  background-color: #000000;\n  padding: 0;\n  box-sizing: border-box;\n  margin: 0;\n  background-repeat: no-repeat;\n  background-position: top center;\n  > * {\n    box-sizing: border-box;\n  }\n}\n\n// Responsive settings\n$screenSizes: (\n        desktop: 1200px,\n        tablet: 991px,\n        mobile: 768px,\n        mobile-small: 480px,\n);\n\n@mixin screen-gt($name) {\n  @media only screen and (min-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin screen-lt($name) {\n  @media only screen and (max-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin container-size() {\n  width: 1320px;\n  margin: auto;\n  padding: 0 20px;\n  @include screen-lt(desktop) {\n    width: 100%;\n  }\n  @include screen-lt(mobile-small) {\n    padding: 0 10px;\n  }\n}\n\n@mixin bgImage($image) {\n  background-repeat: no-repeat;\n  background-position: top center;\n  background-image: url($image);\n  @include screen-lt(mobile-small) {\n    background-position: 51% 0;\n  }\n}","@import \"./src/theme/global.scss\";\n\n.rating {\n  &__container {\n    @include container-size();\n    z-index: 2;\n    align-items: center;\n  }\n  &__image {\n    width: 400px;\n    @include screen-lt(mobile-small) {\n      width: 250px;\n    }\n  }\n  h2 {\n    text-transform: uppercase;\n    font-size: 36px;\n    @include main-font(700);\n    position: relative;\n    padding: 0 15px;\n    z-index: 2;\n    @include screen-lt(mobile-small) {\n      font-size: 24px;\n    }\n    &:before {\n      content: '';\n      width: 80%;\n      height: 45px;\n      @include screen-lt(mobile-small) {\n        height: 28px;\n      }\n      background-color: $orange;\n      top: 0;\n      transform: skew(40deg);\n      left: 0;\n      position: absolute;\n      z-index: -1;\n    }\n    &:after {\n      content: '';\n      width: 80%;\n      height: 45px;\n      @include screen-lt(mobile-small) {\n        height: 28px;\n      }\n      background-color: $orange;\n      top: 0;\n      transform: skew(-40deg);\n      right: 0;\n      position: absolute;\n      z-index: -1;\n    }\n  }\n}"]}]);

