nly screen and (max-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin container-size() {\n  width: 1320px;\n  margin: auto;\n  padding: 0 20px;\n  @include screen-lt(desktop) {\n    width: 100%;\n  }\n  @include screen-lt(mobile-small) {\n    padding: 0 10px;\n  }\n}\n\n@mixin bgImage($image) {\n  background-repeat: no-repeat;\n  background-position: top center;\n  background-image: url($image);\n  @include screen-lt(mobile-small) {\n    background-position: 51% 0;\n  }\n}","@import \"./src/theme/global.scss\";\n\n.guild {\n  &__container {\n    @include container-size();\n    align-items: center;\n    justify-content: center;\n\n    input {\n      height: 100%;\n      width: 100%;\n      display: flex;\n      border: 4px solid $orange;\n      background-color: transparent;\n      @include placeholder-color(#fff);\n    }\n    h2 {\n      text-transform: uppercase;\n      font-size: 36px;\n      @include main-font(700);\n      position: relative;\n      margin-top: 50px;\n      text-align: center;\n      padding: 0 15px;\n      z-index: 2;\n    }\n  }\n  &__inner {\n    display: flex;\n    align-items: center;\n    justify-content: center;\n  }\n  &__widget {\n    position: relative;\n    max-width: 500px;\n    height: 60px;\n    overflow: hidden;\n    margin-top: 50px;\n    .layout_column {\n      height: 100%;\n    }\n  }\n  &__button {\n    position: absolute;\n    z-index: 2;\n    width: 60px;\n    height: calc(100% - 8px);\n    right: -64px;\n    top: 4px;\n    cursor: pointer;\n    border: 4px solid transparent;\n    background-color: transparent;\n    transition: .2s ease;\n    outline: none;\n    &:hover {\n      background-color: #46843640;\n    }\n    &_valid {\n      right: 4px;\n    }\n    img {\n      width: 60%;\n    }\n  }\n  &__info {\n    font-size: 13px;\n    color: #fff;\n    margin-top: 5px;\n    cursor: pointer;\n    background-color: transparent;\n    outline: none;\n    border: none;\n  }\n}"]}]);

