-weight: $weight;\n}\n\nh1,h2,h3,h4,h5,h6,p {\n  padding: 0;\n  margin: 0;\n  font-size: 16px;\n}\nul,ol {\n  padding: 0;\n  margin: 0;\n  font-size: 0;\n}\ndiv {\n  &:focus {\n    outline: 0;\n  }\n}\na {\n  text-decoration: none;\n  color: $black;\n  font-size: 16px;\n}\nbody {\n  font-size: 13px;\n  @include main-font(500);\n  background-color: #000000;\n  padding: 0;\n  box-sizing: border-box;\n  margin: 0;\n  background-repeat: no-repeat;\n  background-position: top center;\n  > * {\n    box-sizing: border-box;\n  }\n}\n\n// Responsive settings\n$screenSizes: (\n        desktop: 1200px,\n        tablet: 991px,\n        mobile: 768px,\n        mobile-small: 480px,\n);\n\n@mixin screen-gt($name) {\n  @media only screen and (min-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin screen-lt($name) {\n  @media only screen and (max-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin container-size() {\n  width: 1320px;\n  margin: auto;\n  padding: 0 20px;\n  @include screen-lt(desktop) {\n    width: 100%;\n  }\n  @include screen-lt(mobile-small) {\n    padding: 0 10px;\n  }\n}\n\n@mixin bgImage($image) {\n  background-repeat: no-repeat;\n  background-position: top center;\n  background-image: url($image);\n  @include screen-lt(mobile-small) {\n    background-position: 51% 0;\n  }\n}","@import \"./src/theme/global.scss\";\n\n.info {\n  position: relative;\n  &__container {\n    @include container-size();\n    z-index: 2;\n    @include screen-lt(mobile) {\n      margin-top: 170px;\n    }\n  }\n  &__item {\n    width: 100%;\n    display: flex;\n    align-items: center;\n    flex-direction: column;\n\n    p {\n      font-size: 24px;\n      color: $white;\n      text-align: center;\n      max-width: 80%;\n      margin-top: 40px;\n      @include screen-lt(mobile) {\n        font-size: 12px;\n        margin-top: 20px;\n      }\n      @include screen-lt(mobile) {\n        font-size: 10px;\n      }\n    }\n  }\n  &__wrapper {\n    width: 230px;\n    height: 230px;\n    border: 6px solid $orange;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    border-radius: 50%;\n    transition: .3s ease-in;\n    z-index: 2;\n    position: relative;\n    overflow: hidden;\n    @include screen-lt(tablet) {\n      width: 180px;\n      height: 180px;\n    }\n    @include screen-lt(mobile) {\n      width: 84px;\n      height: 84px;\n    }\n    @include screen-lt(mobile) {\n      width: 64px;\n      height: 64px;\n    }\n    &:after {\n      content: '';\n      position: absolute;\n      background: linear-gradient(#F05423, #FF8D36);\n      box-shadow: inset 0 0 60px rgba(0,0,0,0.7);\n      top: 0;\n      opacity: 0;\n      left: 0;\n      width: 100%;\n      height: 100%;\n    }\n    &:hover {\n      box-shadow: 0 0 25px 5px rgba(253, 133, 52, 0.6);\n      &:after {\n        transition: .3s ease-in;\n        z-index: 1;\n        opacity: 1;\n      }\n    }\n    img {\n      z-index: 3;\n      @include screen-lt(mobile) {\n        width: 60%;\n      }\n    }\n  }\n  &__asset {\n    position: absolute;\n    top: 0;\n    left: 0;\n    z-index: 1;\n    width: 316px;\n    height: 266px;\n    @include screen-lt(mobile) {\n      width: 150px;\n      height: auto;\n    }\n    &_right {\n      right: 0;\n      left: auto;\n    }\n  }\n}\n\n"]}]);

