op: 20px;\n      }\n    }\n    &_bottom {\n      margin-bottom: 10px;\n      &_x2 {\n        margin-bottom: 20px;\n      }\n    }\n  }\n}\n\n@mixin base-aligns() {\n  &__align {\n    &_left {\n      text-align: left;\n    }\n    &_center {\n      text-align: center;\n    }\n    &_right {\n      text-align: right;\n    }\n  }\n}\n\n@mixin main-font($weight) {\n  font-family: \"Montserrat\", sans-serif;\n  font-weight: $weight;\n}\n\nh1,h2,h3,h4,h5,h6,p {\n  padding: 0;\n  margin: 0;\n  font-size: 16px;\n}\nul,ol {\n  padding: 0;\n  margin: 0;\n  font-size: 0;\n}\ndiv {\n  &:focus {\n    outline: 0;\n  }\n}\na {\n  text-decoration: none;\n  color: $black;\n  font-size: 16px;\n}\nbody {\n  font-size: 13px;\n  @include main-font(500);\n  background-color: #000000;\n  padding: 0;\n  box-sizing: border-box;\n  margin: 0;\n  background-repeat: no-repeat;\n  background-position: top center;\n  > * {\n    box-sizing: border-box;\n  }\n}\n\n// Responsive settings\n$screenSizes: (\n        desktop: 1200px,\n        tablet: 991px,\n        mobile: 768px,\n        mobile-small: 480px,\n);\n\n@mixin screen-gt($name) {\n  @media only screen and (min-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin screen-lt($name) {\n  @media only screen and (max-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin container-size() {\n  width: 1320px;\n  margin: auto;\n  padding: 0 20px;\n  @include screen-lt(desktop) {\n    width: 100%;\n  }\n  @include screen-lt(mobile-small) {\n    padding: 0 10px;\n  }\n}\n\n@mixin bgImage($image) {\n  background-repeat: no-repeat;\n  background-position: top center;\n  background-image: url($image);\n  @include screen-lt(mobile-small) {\n    background-position: 51% 0;\n  }\n}","@import \"./src/theme/global.scss\";\n\n.pro {\n  background-image: url('../../../../statics/pro_bg.png');\n  @include screen-lt(mobile) {\n    background-image: url('../../../../statics/pro_bg_m.png');\n  }\n  background-position: center center;\n  background-size: cover;\n  background-repeat: no-repeat;\n  width: 100%;\n  min-height: 1140px;\n  @include screen-lt(mobile) {\n    min-height: auto;\n    padding-bottom: 20px;\n  }\n  &__logo {\n    width: 200px;\n    margin-top: 80px;\n    @include screen-lt(mobile) {\n      width: 20%;\n      margin-top: 0;\n    }\n  }\n\n  &__text {\n    display: flex;\n    align-items: center;\n    flex-direction: column;\n  }\n  &__container {\n    justify-content: space-between;\n    min-height: 930px;\n    @include container-size();\n    @include screen-lt(mobile) {\n      min-height: auto;\n    }\n  }\n  h2 {\n    font-size: 100px;\n    @include main-font(700);\n    text-transform: uppercase;\n    margin-top: 20px;\n    color: $white;\n    @include screen-lt(tablet) {\n      font-size: 70px;\n    }\n    @include screen-lt(mobile) {\n      font-size: 35px;\n    }\n    @include screen-lt(mobile-small) {\n      font-size: 27px;\n    }\n  }\n  p {\n    font-size: 30px;\n    color: $white;\n    @include screen-lt(mobile) {\n      font-size: 20px;\n    }\n    @include screen-lt(mobile-small) {\n      font-size: 13px;\n    }\n  }\n  &__footer {\n    align-items: center;\n    justify-content: center;\n    @include screen-lt(mobile) {\n      margin-top: 70px;\n    }\n    img {\n      width: 142px;\n      height: 142px;\n      @include screen-lt(mobile) {\n        width: 57px;\n        height: auto;\n      }\n      @include screen-lt(mobile-small) {\n        width: 47px;\n      }\n      &:first-child {\n        width: 164px;\n        height: auto;\n        @include screen-lt(mobile) {\n          width: 86px;\n        }\n        @include screen-lt(mobile-small) {\n          width: 66px;\n        }\n      }\n    }\n    p {\n      font-size: 18px;\n      white-space: nowrap;\n      @include screen-lt(mobile) {\n        font-size: 10px;\n      }\n    }\n  }\n}"]}]);

