n    color: $color;\n  }\n\n  &:-ms-input-placeholder {\n    color: $color;\n  }\n}\n\n@mixin base-margins() {\n  &__margin {\n    &_left {\n      margin-left: 10px;\n      &_x2 {\n        margin-left: 20px;\n      }\n    }\n    &_right {\n      margin-right: 10px;\n      &_x2 {\n        margin-right: 20px;\n      }\n    }\n    &_top {\n      margin-top: 10px;\n      &_x2 {\n        margin-top: 20px;\n      }\n    }\n    &_bottom {\n      margin-bottom: 10px;\n      &_x2 {\n        margin-bottom: 20px;\n      }\n    }\n  }\n}\n\n@mixin base-aligns() {\n  &__align {\n    &_left {\n      text-align: left;\n    }\n    &_center {\n      text-align: center;\n    }\n    &_right {\n      text-align: right;\n    }\n  }\n}\n\n@mixin main-font($weight) {\n  font-family: \"Montserrat\", sans-serif;\n  font-weight: $weight;\n}\n\nh1,h2,h3,h4,h5,h6,p {\n  padding: 0;\n  margin: 0;\n  font-size: 16px;\n}\nul,ol {\n  padding: 0;\n  margin: 0;\n  font-size: 0;\n}\ndiv {\n  &:focus {\n    outline: 0;\n  }\n}\na {\n  text-decoration: none;\n  color: $black;\n  font-size: 16px;\n}\nbody {\n  font-size: 13px;\n  @include main-font(500);\n  background-color: #000000;\n  padding: 0;\n  box-sizing: border-box;\n  margin: 0;\n  background-repeat: no-repeat;\n  background-position: top center;\n  > * {\n    box-sizing: border-box;\n  }\n}\n\n// Responsive settings\n$screenSizes: (\n        desktop: 1200px,\n        tablet: 991px,\n        mobile: 768px,\n        mobile-small: 480px,\n);\n\n@mixin screen-gt($name) {\n  @media only screen and (min-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin screen-lt($name) {\n  @media only screen and (max-width: #{map-get($screenSizes, $name)}) {\n    @content;\n  }\n}\n\n@mixin container-size() {\n  width: 1320px;\n  margin: auto;\n  padding: 0 20px;\n  @include screen-lt(desktop) {\n    width: 100%;\n  }\n  @include screen-lt(mobile-small) {\n    padding: 0 10px;\n  }\n}\n\n@mixin bgImage($image) {\n  background-repeat: no-repeat;\n  background-position: top center;\n  background-image: url($image);\n  @include screen-lt(mobile-small) {\n    background-position: 51% 0;\n  }\n}","@import \"./src/theme/global.scss\";\n\n@mixin pseudo($left, $right) {\n  content: '';\n  top: 6px;\n  left: $left;\n  right: $right;\n  position: absolute;\n  height: 2px;\n  width: 110px;\n  background-color: $orange;\n  @include screen-lt(mobile) {\n    width: 90px;\n  }\n}\n\n.social {\n  background-color: transparent;\n  height: 30px;\n  width: 30px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  border: 2px solid $orange;\n  border-radius: 50%;\n  margin-right: 15px;\n  outline: none;\n  cursor: pointer;\n  &:last-child {\n    margin-right: 0;\n  }\n  &:hover {\n    opacity: .7;\n  }\n\n  &__container {\n    margin: 20px 0;\n  }\n\n  img {\n    width: 13px;\n    height: 13px;\n    min-width: 13px;\n    min-height: 13px;\n  }\n}\n\n.auth {\n  &__or {\n    position: relative;\n    &:before {\n      @include pseudo(0, auto);\n    }\n    &:after {\n      @include pseudo(auto, 0);\n    }\n  }\n  &__link {\n    color: $brown;\n    text-decoration: underline;\n    font-size: 12px;\n    margin-left: 5px;\n  }\n  &__button {\n    margin-top: 20px;\n    &_more {\n      text-decoration: underline;\n      color: $brown;\n      background-color: transparent;\n      text-transform: none;\n      width: auto;\n      &:hover {\n        background-color: transparent;\n        color: $white;\n        transition: .1s;\n      }\n    }\n  }\n  &__error {\n    color: #FF2120;\n    font-size: 12px;\n    padding-top: 10px;\n  }\n}"]}]);

